<template>
    <div>
        <van-nav-bar
        title="书籍详情"
        left-text="返回" 
        @click-left="toback"
        :fixed="true"
        />
        <div class="book-name clearfix">
            <div class="name-img fl">
                <img :src="bookData.cover" alt="" class="auto-img">
            </div>
            <div class="name-msg fl">
                <div class="one-text">{{bookData.title}}</div>
                <div>{{bookData.author}}</div>
                <div>
                    <span>{{bookData.majorCate}}</span>
                    <span>|</span>
                    <span>连载</span>
                </div>
            </div>
        </div>
        <div class="book-massage clearfix">
            <div class="fl">
                <div>7.9</div>
                <div><span>5000</span><span>人评论</span></div>
            </div>
            <div class="fl">
                <div>1.5万</div>
                <div>阅读过</div>
            </div>
            <div class="fl">
                <div>{{bookData.totalFollower}}</div>
                <div>人气值</div>
            </div>
            <div class="fl">
                <div>{{bookData.wordCount}}</div>
                <div>字数</div>
            </div>
        </div>
        <div class="book-info">
            {{bookData.longIntro}}
        </div>
        <div class="book-last clearfix">
            <div class="fl">目录</div>
            <div class="fl last-center one-text">{{bookData.lastChapter}}</div>
            <div class="fr">{{bookData.updated}}更新</div>
        </div>
        <div class="book-author" v-if="!isShow">
            <div class="author-title">作者其他书籍</div>
            <div class="author-list">
                <div class="author-list-one clearfix" v-for="(item,index) in authorData" :key="index" @click="getBook(item._id)">
                    <div class="fl author-img">
                        <img :src="item.cover" alt="" class="auto-img">
                    </div>
                    <div class="fl author-msg">
                        <div>{{item.title}}</div>
                        <div>{{item.author}}</div>
                        <div>dasdsddsadsadsads</div>
                    </div>
                    <div class="fr author-see">
                        查看
                    </div>
                </div>
            </div>
            <div class="author-bottom">
                    查看更多同作者作品
            </div>
        </div>
        <div class="you-like">
            <div class="author-title">你可能感兴趣的书</div>
            <div class="like-list">
                <div class="like-list-width clearfix" ref="widthH">
                    <div class="like-list-one fl" v-for="(item,index) in likeData" :key="index" @click="getBook(item._id)">
                        <div>
                            <img :src="item.cover" alt="" class="auto-img">
                        </div>
                        <div class="one-text">
                            {{item.title}}
                        </div>
                        <div>
                            {{item.author}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="book-fixed">
            <div class="fl" @click="addRack" :class="{'active':rack}">{{rack?addrack.torack:addrack.outrack}}</div>
            <div class="fl" @click="go()">开始阅读</div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                id:"",
                bookData:[],
                isShow:"",
                authorData:[],
                allauthorData:[],
                likeData:[],
                addrack:{
                    torack:"已加入书架",
                    outrack:"加入书架"
                },
                rack:false,
            }
        },
        created(){
            this.id = this.$route.query.id;
            
            this.getBook(this.id)
        },
        methods:{
            //看是否已加入书架
            isRack(){
                let lsAll = localStorage.valueOf()
                let a = 'rackList'+this.bookData._id
                let arr= []
                for(let key in lsAll){
                    arr.push(key)
                }
                if( arr.indexOf(a) == -1 ){
                    this.rack = false
                }else{
                    this.rack = true
                }
            },
            // 加书架
            addRack(){
                
                if(this.rack){
                    return
                }

                
                
                localStorage.setItem("rackList"+this.bookData._id,JSON.stringify(this.bookData))

                
                this.rack = true 
            },
            //开始阅读
            go(){
                
                this.$router.push({name:"Bookmsg",query:{id:this.bookData._id,title:this.bookData.title}})
            },
            // 图片处理
            imgKey(value) {
                return unescape(value).slice(7);
            },
            // 获取书籍
            getBook(id){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "book-info/"+id,
                        
                    }).then(result => {
                        this.$toast.clear(); 
                        
                        result.data.cover = this.imgKey(result.data.cover)
                        result.data.updated = result.data.updated.slice(0,10)

                        result.data.wordCount = result.data.wordCount>10000? String(result.data.wordCount).slice(0,String(result.data.wordCount).length-4) + "万":result.data.wordCount

                        result.data.totalFollower = result.data.totalFollower>10000? String(result.data.totalFollower).slice(0,String(result.data.totalFollower).length-4) + "万" :result.data.totalFollower

                        this.bookData = result.data 
                        
                        this.getreview(this.bookData.author)
                        this.getLike(this.bookData._id)
                        this.isRack();
                    })
                    .catch(err => {
                        this.$toast.clear();
                    });
            },
            //获取同作者相关书籍
            getreview(id){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/author-books?author="+id,
                       
                        
                    }).then(result => {
                        this.$toast.clear(); 
                        this.authorData = [];
                        if(result.data.books.length==0){
                            this.isShow = true ;
                            return
                        }

                        for(let i = 0 ; i < result.data.books.length ; i++){
                            result.data.books[i].cover = this.imgKey(result.data.books[i].cover)
                        }

                        this.allauthorData = result.data.books 

                        for(let i = 0 ; i < 3 ; i++){
                            this.authorData.push(result.data.books[i])  
                        }

                        

                        
                        

                    })
                    .catch(err => {
                        this.$toast.clear();
                    });
            },
            toback(){
                this.$router.go(-1)
            },
            // 获取相关书籍
            getLike(id){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/recommend/"+id,
                       
                        
                    }).then(result => {
                        this.$toast.clear(); 
                        
                        this.$refs.widthH.style.width = 86*result.data.books.length+10 + "px"
                        for(let i = 0 ; i < result.data.books.length ; i ++){
                            result.data.books[i].cover = this.imgKey(result.data.books[i].cover)
                        }
                        this.likeData = result.data.books
                        
                    })
                    .catch(err => {
                        this.$toast.clear();
                    });
            }
        }
    }
</script>

<style lang="less" scoped>
.active{
    color: #ccc;
}
.book-fixed{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 50px;
    background-color:#fff;
    font-size: 14px;
    color: #000;
    
    
    >div{
        width: 45%;
        // background-color: chocolate;
        margin-left: 13px;
        height: 40px;
        margin-top: 5px;
        border-radius: 5px;
        text-align: center;
        line-height: 40px;
    }
    >div:last-child{
        color: #fff;
        background-color: #05d390;
    }
}
.like-list-one{
    width: 70px;
    margin:0 8px;
    >div{
        color: #000;
    }
    >div:first-child{
        width: 70px;
        border-radius: 10px;
        overflow: hidden;
    }
    >div:last-child{
        color:#ccc ;
    }
}
.like-list{
    margin: 5px 15px;
    
    overflow-x: scroll;
    overflow-y: hidden;
    margin-bottom: 50px;
}
.like-list::-webkit-scrollbar {
    display: none;
}
.author-bottom{
    text-align: center;
    font-size: 14px;
    padding: 8px 0;
    margin-top: 3px;
    background-color: #fff;
}
.author-title{
    padding: 5px 15px;
    margin-top: 5px;
    background-color: #fff;
    font-size: 16px;
    font-weight: 700;

}
.author-list{
    padding: 15px;
    background: #fff;
}
.author-list-one{
    padding: 5px;
    .author-img{
        width: 60px;
        border-radius: 10px;
        overflow: hidden;
    }
    .author-msg{
        margin-left: 15px;
        >div:first-child{
            font-size: 16px;
            font-weight: 700;
            color: #000;
        }
        >div{
            margin-top: 6px;
            color: #ccc;
        }
    }
    .author-see{
        font-size: 14px;
        padding: 5px 15px;
        color: #4dbb97;
        border-radius: 15px;
        margin-top: 30px;
        background-color: #f5f6fa;
    }
}
.book-last{
    margin-top: 3px;
    padding: 15px 20px;
    background-color: #fff;
    line-height: 20px;
    color: #c6c7cb;
    .last-center{
        width: 190px;
    }
    >div:first-child{
        color: #000;
        font-size: 16px;
        font-weight: 700;
        margin-right: 10px;
    }
}
.book-info{
    height: 60px;
    line-height: 25px;
    background-color: #fff;
    padding: 15px 15px;
    font-size: 14px;
    color: #404143;
    overflow : hidden;
    // white-space: nowrap;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}
.book-name{
    margin-top: 46px;
    padding: 15px;
    background-color: #fff;
    .name-img{
        width: 120px;
    }
    .name-msg{
        margin-left: 10px;
        >div{
            margin-bottom: 7px;
            color: #f88070;
        }
        >div:first-child{
            font-size: 16px;
            color: black;
            font-weight: 700;
            width: calc(~"100%-120px");
        }
        >div:last-child{
            color: #ccc;
            >span{
                margin-right: 5px;
            }
        }
    }
}
.book-massage{
    background-color: #fff;
    >.fl{
        text-align: center;
        width: 25%;
        
        >div:first-child{
            color: black;
            font-size: 16px;
            font-weight: 700;
        }
        >div:last-child{
            color: #ccc;
            
        }
    }
}
</style>